.feedback {
   padding: 16px 16px 0 16px;

   &-text {
      font-size: 14px;
      font-family: 'Lato Regular';
      line-height: 21px;
   }

   &-link {
      display: inline-flex;
      margin-top: 1rem;
      padding: 8px 12px;
      border: 1px solid map-get($color, info);
      border-radius: 2px;
      background-color: map-get($color, light);
      color: map-get($color, dark);
      font-weight: bold;

      &__icon {
         margin-right: 8px;
      }

      &__text {
         font-size: 16px;
         line-height: 24px;
      }

      &:hover {
         background-color: darken(#edf4ee, 20%);
         color: map-get($color, dark);
         text-decoration: none;
      }
   }

   &-list {
      list-style: none;

      &__item {
         position: relative;

         &::before {
            position: absolute;
            left: 0;
            margin-left: -1em;
            width: 1em;
            color: black;
            content: '•';
         }
      }
   }
}
